
.cartControl {
  font-size: 0;
}
.cartControl .cartDecrease {
  display: inline-block;
  padding: 6px;
  opacity: 1;
  transition: translate3d(0, 0, 0);
}
.cartControl .cartDecrease .inner {
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cartControl .cartDecrease.move-enter-active,
.cartControl .cartDecrease.move-leave-active {
  transition: all 0.4s linear;
}
.cartControl .cartDecrease.move-enter,
.cartControl .cartDecrease.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(24px, 0, 0);
          transform: translate3d(24px, 0, 0);
}
.cartControl .cartDecrease.move-enter .inner,
.cartControl .cartDecrease.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.cartControl .cartCount {
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding-top: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: #93999f;
}
.cartControl .cartAdd {
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: #00a0dc;
}


