
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitSC-Light', 'Helvetica-Light', Arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: "";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (min-device-pixel-radio: 1.5), (-webkit-min-device-pixel-radio: 1.5) {
.border-1px:after {
    transform: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
}
}
@media (min-device-pixel-radio: 2), (-webkit-min-device-pixel-radio: 2) {
.border-1px:after {
    transform: scaleY(0.5);
    -webkit-transform: scaleY(0.5);
}
}
.tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:after {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.tab .tab-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
}
.tab .tab-item a {
  display: block;
  font-size: 14px;
  color: #4d555d;
}
.tab .tab-item a.active {
  color: #f01414;
}


