.food {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.food.move-enter-active,
.food.move-leave-active {
  transition: all 0.2s linear;
}
.food.move-enter,
.food.move-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.food .food-content .img-header {
  position: relative;
  width: 100%;
  height: 0;
  padding-top: 100%;
}
.food .food-content .img-header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.food .food-content .img-header .back {
  position: absolute;
  top: 10px;
  left: 0px;
}
.food .food-content .img-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  font-size: 20px;
  color: #fff;
}
.food .food-content .content {
  padding: 18px;
  position: relative;
}
.food .food-content .content .title {
  line-height: 14px;
  font-size: 14px;
  margin-bottom: 8px;
  color: #07111b;
  font-weight: 700;
}
.food .food-content .content .detail {
  margin-bottom: 18px;
  line-height: 10px;
  font-size: 0;
}
.food .food-content .content .detail .sell-count,
.food .food-content .content .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.food .food-content .content .detail .sell-count {
  margin-right: 12px;
}
.food .food-content .content .price {
  line-height: 24px;
}
.food .food-content .content .price .new-price {
  font-size: 14px;
  color: #f01414;
  font-weight: 700;
}
.food .food-content .content .price .old-price {
  text-decoration: line-through;
  font-size: 10px;
  color: #8f9d9f;
  font-weight: 700;
}
.food .food-content .content .cartControl-wrap {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.food .food-content .content .buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  height: 24px;
  line-height: 24px;
  padding: 0 12px;
  box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
  opacity: 1;
}
.food .food-content .content .buy.fade-enter-active,
.food .food-content .content .buy.fade-leave-active {
  transition: all 0.2s;
}
.food .food-content .content .buy.fade-enter,
.food .food-content .content .buy.fade-leave-active {
  opacity: 0;
  z-index: -1;
}
.food .food-content .food-desc {
  padding: 18px;
}
.food .food-content .food-desc .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.food .food-content .food-desc .text {
  line-height: 16px;
  font-size: 12px;
  font-weight: 200;
  color: #4d555d;
  padding: 0 6px;
}
.food .food-content .rating {
  padding-top: 18px;
}
.food .food-content .rating .title {
  line-height: 14px;
  font-size: 14px;
  margin-left: 18px;
  color: #07111b;
}
.food .food-content .rating .rating-wrap {
  padding: 0px 18px;
}
.food .food-content .rating .rating-wrap .rating-item {
  position: relative;
  padding: 16px 0;
  position: relative;
}
.food .food-content .rating .rating-wrap .rating-item:after {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.food .food-content .rating .rating-wrap .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  line-height: 12px;
  font-size: 0;
}
.food .food-content .rating .rating-wrap .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  margin-right: 6px;
}
.food .food-content .rating .rating-wrap .rating-item .user .avatar {
  border-radius: 50%;
}
.food .food-content .rating .rating-wrap .rating-item .time {
  margin-bottom: 6px;
  line-height: 12px;
  font-size: 10px;
  color: #93999f;
}
.food .food-content .rating .rating-wrap .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  color: #07111b;
}
.food .food-content .rating .rating-wrap .rating-item .text .icon-thumb_up,
.food .food-content .rating .rating-wrap .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.food .food-content .rating .rating-wrap .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.food .food-content .rating .rating-wrap .rating-item .text .icon-thumb_down {
  color: #93999f;
}
.food .food-content .rating .rating-wrap .no-rating {
  padding: 16px 0;
  font-size: 12px;
  color: #93999f;
}


