
.ratingSelect .ratingType {
  padding: 18px 0;
  margin: 0 18px;
  position: relative;
  font-size: 0;
}
.ratingSelect .ratingType:after {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingSelect .ratingType .block {
  display: inline-block;
  line-height: 16px;
  padding: 8px 12px;
  margin-right: 8px;
  border-radius: 1px;
  font-size: 12px;
  color: #4d555d;
}
.ratingSelect .ratingType .block.active {
  color: #fff;
}
.ratingSelect .ratingType .block .count {
  margin-left: 2px;
  line-height: 16px;
}
.ratingSelect .ratingType .block.positive {
  background: rgba(0,160,220,0.2);
}
.ratingSelect .ratingType .block.positive.active {
  background: #00a0dc;
}
.ratingSelect .ratingType .block.negative {
  background: rgba(77,85,93,0.2);
}
.ratingSelect .ratingType .block.negative.active {
  background: #4d555d;
}
.ratingSelect .switch {
  padding: 12px 18px;
  line-height: 24px;
  font-size: 12px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  color: #93999f;
  font-size: 0;
}
.ratingSelect .switch.active .icon-check_circle {
  color: #00c850;
}
.ratingSelect .switch .icon-check_circle {
  display: inline-block;
  vertical-align: top;
  margin-right: 4px;
  font-size: 24px;
}
.ratingSelect .switch .text {
  font-size: 12px;
}


