.shopCart {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 50;
  width: 100%;
  height: 48px;
  background-color: #7fffd4;
}
.shopCart .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #141d27;
  font-size: 0;
}
.shopCart .content .content-left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.shopCart .content .content-left .logo-wrap {
  display: inline-block;
  position: relative;
  top: -10px;
  margin: 0 16px;
  padding: 6px;
  width: 56px;
  height: 56px;
  box-sizing: border-box;
  vertical-align: top;
  border-radius: 50%;
  background: #141d27;
}
.shopCart .content .content-left .logo-wrap .num {
  position: absolute;
  top: 0;
  right: 0;
  width: 24px;
  height: 16px;
  line-height: 16px;
  text-align: center;
  border-radius: 50%;
  font-size: 9px;
  font-weight: 700;
  color: #fff;
  background: #f01414;
  box-shadow: 0 4px 8px 0 #000;
}
.shopCart .content .content-left .logo-wrap .logo {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: #2b343c;
  text-align: center;
}
.shopCart .content .content-left .logo-wrap .logo.highlight {
  background: #00a0dc;
}
.shopCart .content .content-left .logo-wrap .logo .icon-shopping_cart {
  line-height: 44px;
  font-size: 24px;
  color: #80858a;
}
.shopCart .content .content-left .logo-wrap .logo .icon-shopping_cart.highlight {
  color: #fff;
}
.shopCart .content .content-left .price {
  display: inline-block;
  vertical-align: top;
  margin-top: 12px;
  line-height: 24px;
  font-size: 18px;
  padding-right: 12px;
  box-sizing: border-box;
  border-right: 1px solid rgba(255,255,255,0.1);
  font-weight: 700;
  color: #919396;
}
.shopCart .content .content-left .price.highlight {
  color: #fff;
}
.shopCart .content .content-left .desc {
  display: inline-block;
  margin: 12px 0 0 12px;
  line-height: 24px;
  font-size: 10px;
  color: #919396;
}
.shopCart .content .content-right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
  width: 105px;
  background: #2b333b;
}
.shopCart .content .content-right .pay {
  height: 48px;
  line-height: 48px;
  text-align: center;
  font-weight: 700;
  font-size: 12px;
  color: #919396;
}
.shopCart .content .content-right .pay.unenough {
  background: #2b333b;
}
.shopCart .content .content-right .pay.enough {
  background: #00b43c;
  color: #fff;
}
.shopCart .ball {
  position: fixed;
  left: 32px;
  bottom: 22px;
  z-index: 200;
  transition: all 0.6s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
.shopCart .ball .inner {
  background: #00a0dc;
  width: 16px;
  height: 16px;
  transition: all 0.6s linear;
  border-radius: 50%;
}
.shopCart .shopCart-list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0);
}
.shopCart .shopCart-list.fold-enter-active,
.shopCart .shopCart-list.fold-leave-active {
  transition: all 0.5s;
}
.shopCart .shopCart-list.fold-enter,
.shopCart .shopCart-list.fold-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopCart .shopCart-list .list-header {
  height: 40px;
  line-height: 40px;
  padding: 0 18px;
  background: #f3f5f7;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.shopCart .shopCart-list .list-header .title {
  float: left;
  font-size: 14px;
  color: #07111b;
}
.shopCart .shopCart-list .list-header .empty {
  float: right;
  font-size: 12px;
  color: #00a0dc;
}
.shopCart .shopCart-list .list-item {
  padding: 0 18px;
  max-height: 217px;
  overflow: hidden;
  background: #fff;
}
.shopCart .shopCart-list .list-item .food {
  position: relative;
  padding: 12px 0;
  box-sizing: border-box;
  position: relative;
}
.shopCart .shopCart-list .list-item .food:after {
  position: absolute;
  left: 0px;
  bottom: 0px;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.shopCart .shopCart-list .list-item .food .name {
  line-height: 24px;
  font-size: 14px;
  color: #07111b;
}
.shopCart .shopCart-list .list-item .food .price {
  position: absolute;
  right: 90px;
  bottom: 12px;
  line-height: 24px;
  font-size: 14px;
  font-weight: 700px;
  color: #f01414;
}
.shopCart .shopCart-list .list-item .food .cartControlWarp {
  position: absolute;
  bottom: 6px;
  right: 0px;
}
.list-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 40;
  opacity: 1;
  background: rgba(7,17,27,0.6);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.list-mask.fade-enter-active,
.list-mask.fade-leave-active {
  transition: all 0.5s;
}
.list-mask.fade-enter,
.list-mask.fade-leave-active {
  opacity: 0;
  background: rgba(7,17,27,0);
}


